#map {
  z-index: 1;
  height: 100%;
}

#myInput {
  text-align: center;
  width: 100%;
  font-size: 16px;
  padding: 12px 20px 12px 40px;
  border: none;
}

#myUL li a {
  text-decoration: none;
  color: black;
  display: block;
  margin-bottom: 8px;
  padding: 12px;
  font-size: 18px;
  text-align: center;
  cursor: pointer;
}

#myUL li a:hover {
  background-color: #eee;
}

.menu-button {
  border-radius: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 10;
  margin-bottom: 24px;
  cursor: pointer;
}

.menu-button.w--open {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 10;
  background-color: transparent;
  color: #757575;
  cursor: pointer;
}

.icon::before {
  border-radius: 100%;
  font-size: 42px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.4);
  color: #000;
}

.navbar {
  background-color: transparent;
}

.container {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  background-color: transparent;
}

.nav-menu {
  width: 30%;
  background-color: transparent;
  background-image: -webkit-linear-gradient(45deg, rgba(117, 127, 154, 0.45), rgba(215, 221, 232, 0.68));
  background-image: linear-gradient(45deg, rgba(117, 127, 154, 0.45), rgba(215, 221, 232, 0.68));
  position: relative;
}


